let jweixin = require('jweixin-module/out/index.js')

export default {
	//  
	isWechat: function() {
		var ua = window.navigator.userAgent.toLowerCase();
		if (ua.match(/micromessenger/i) == 'micromessenger') {
			return true;
		} else {
			return false;
		}
	},
	//sdk  
	initJssdk: function(callback) {
		let headers = {};
		let url = location.href.split('#')[0] //
		headers['content-type'] = 'application/json';
		uni.request({
			url: 'https://www.jimuzhou.com/api/sample.php', //urlmain.js
			method: 'POST', //getpost
			header: headers,
			data: {
				url: url
			},
			success: res => {
				console.log(jweixin);
				let wx_config = res.data;

				jweixin.config({
					debug: false,
					appId: wx_config.appId,
					timestamp: wx_config.timestamp,
					nonceStr: wx_config.nonceStr,
					signature: wx_config.signature,
					jsApiList: [
						'scanQRCode',
						'checkJsApi',
						'onMenuShareTimeline',
						'closeWindow',
						'updateTimelineShareData',
						'onMenuShareAppMessage'
					]
				});
				//
				if (callback) {
					callback();
				}
			},
		});
	},

	scanQRCode: function() {
		return new Promise((resolve, reject) => {
			this.initJssdk(function() {
				jweixin.ready(function() {
					jweixin.scanQRCode({
						needResult: 1, // 01
						scanType: ["qrCode", "barCode"], // 
						success: function(res) {
							var result = res.resultStr; // needResult  1 

							resolve(result)
						}
					});
				})
			})
		})

	},

	close: function() {
		this.initJssdk(function() {
			jweixin.ready(function() {
				jweixin.closeWindow();
			})
		})
	},
	
	share_pyq: function() {
		this.initJssdk(function() {
			jweixin.ready(function() {
				 jweixin.updateTimelineShareData({ 
				        title: '-Simple and efficient', // 
				        link: 'https://www.jimuzhou.com/api/getUser.php', // JS
				        imgUrl: 'https://bmob-cdn-23134.bmobcloud.com/2019/09/14/19716d4640cbb9ef8085676de06d5604.png', // 
				        success: function () {
				          // 
				        }
				    })
			})
		})
	},

}
